import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  clickCounter: number = 0;
  name: string="";
  // gola: boolean=true;
  constructor() { }

  ngOnInit() {
  
  }
  countClick(){
    this.clickCounter+=1;
   
  }
  countClickReduce(){
    if(this.clickCounter>0){
      this.clickCounter-=1;

    }
  }
 


}
